// @ts-nocheck
import { icons } from './config';
import { IconProps } from './interfaces';
import './index.scss'

const Icon = ({ name, size = 16, fill = 'currentColor', link }: IconProps) => { 
    const IconComponent = icons[name]; 
    if (!IconComponent) { 
      console.error(`Icon "${name}" not found in icons object`); 
      return null; 
    } 
  
    return(
      <a className='iconLink' href={link} target="_blank" rel="noopener noreferrer">

         <IconComponent fill={fill} />
      </a>)
  }; 
  export default Icon;
  